.gpt3__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0 !important;
}

.gtp3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
}

.gpt3__header-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 400;
    line-height: normal;

    margin-top: 1.5rem;
}

.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header-content__input input {
    flex: 2;
    padding: 0 1rem;
    outline: none;
    border: 2px solid var(--color-footer);
    border-radius: 5px 0px 0px 5px;
    background: var(--color-footer);
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
}

.gpt3__header-content__input button {
    padding: 0 1rem;
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    border-radius: 0px 5px 5px 0px;
    border: none;
    background: var(--color-button);
    color: #FFF;
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    outline: none;
}

.gpt3__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.gpt3__header-content__people img {
    width: 182px;
    height: 38px;
}

.gpt3__header-content__people p {
    margin: 0 0 0 1rem;
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 37.611px;
}

.gpt3__header-image {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 750px;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-content {
        margin: 0 0 3rem;
    }
    .gpt3__header-image {
        width: 90%;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content__people p {
        margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .gpt3__header p {
        font-size: 14px;
        line-height: 22px;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}