.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-headding {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 5rem;

    text-align: left;
}

.gpt3__features-headding h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__features-headding p {
    display: flex;
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    margin-top: 2rem;
    
}

.gpt3__features-container {
    flex: 1.5;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

@media screen and (max-width: 990px) {
    .gpt3__features {
        flex-direction: column;
    }
    .gpt3__features-headding {
        margin: 0 0 2rem 0;
    }
    .gpt3__features-headding p {
        margin-bottom: 0.5rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
    .gpt3__features-headding {
        margin-right: 0;
    }
    .gpt3__features-container .gpt3__features-container__feature {
        flex-direction: column;
    }
}
